const quotesSlider = $ => {
	const owl = $('.quotes-slider');
	owl.owlCarousel({
		dots: false,
		items: 1
	});

	const leftBtn = $('.quotes-slider__prev');
	const rightBtn = $('.quotes-slider__next');
	leftBtn.click(() => {
		console.log('click');
		owl.trigger('prev.owl.carousel');
		leftBtn.blur();
	});

	rightBtn.click(() => {
		console.log('click');
		owl.trigger('next.owl.carousel');
		rightBtn.blur();
	});
};

module.exports = quotesSlider;



